import React, { useRef } from "react";
import Parallel from "../animations/Parallel";
import { Parallax } from "react-parallax";
import { spacing } from "../essentials";
import { ProjectPortrait, gifSkills } from "../../db/img/img";

import { themeRGB2 } from "../essentials";
import { skills } from "../../db/db";


const Skills=(props)=>{

    const renderSkills=skills.map(
        (i)=>{
            return(
                <div className="niceCenter">
                    <h5>
                        🦾 {i.name} <br />
                    </h5>
                        <li  className="niceCenter">{i.description}</li> 
                </div>
            )
        }
    )

    
    return(
        <div id="Skills"
        > 
            <div style={{
                textAlign: 'left'
            }}>
                <Parallel
                    r={themeRGB2[props.index].R}
                    g={themeRGB2[props.index].G}
                    b={themeRGB2[props.index].B}
                    text={'Skills harvested so far'}
                ></Parallel> 
            
            <Parallax
                    blur={0} 
                    bgImage={gifSkills[props.index]} 
                    strength={250}                
                    >
                        <br />
                        <br />
                        <div className="emphasize"
                            style={{
                                textAlign: 'left'
                            }}
                            >
                                <div className="">
                                    {renderSkills}
                                    {spacing}
                                </div>
                        </div>

                </Parallax>

            </div>
        </div>

    )
}

export default Skills;