import React, {useState } from "react";
import { useSpring, animated, config } from "react-spring";



const Counter=(props)=>{

    const [flip, set] = useState(false)
    const { number } = useSpring({
      reset: true, 
      from: { number: 0 },
      number: props.count,
      delay: 2000,
      config: config.molasses,
      onRest: () => set(!flip),
    })

    const counter = <span><animated.div>{number.to(n => n.toFixed(0))}</animated.div></span>



    return(
        <div className="fullNav fontColor"
        >
            <hr />
            <br />
            <h2>Number of website visit: {counter} </h2>
            <hr />
            <p>Thank you for your visit !</p>

            <br /><br /><br /><br />

            

        </div>
    )
}

export default Counter;